<template>
  <div class="main">
    <div class="down">
      <img src="../assets/下载.jpg" alt="" />
    </div>
    <div class="content">
      <img src="../assets/米话logo.png" alt="" />
      <div>
        <div class="pic"></div>
      </div>
      <div class="login">
        <img src="../assets/call.png" alt="" />
        <div v-if="showlogin">
          <a-form
            id="components-form-demo-normal-login"
            :form="form"
            class="login-form"
            @submit="handleSubmit"
          >
            <a-form-item>
              <a-input
                v-decorator="[
                  'userName',
                  {
                    rules: [
                      {
                        required: true,
                        message: '请输入你的账号!',
                      },
                    ],
                  },
                ]"
                placeholder="账号"
              >
                <a-icon
                  slot="prefix"
                  type="user"
                  style="color: rgba(0, 0, 0, 0.25)"
                />
              </a-input>
            </a-form-item>
            <a-form-item>
              <a-input-password
                v-decorator="[
                  'password',
                  {
                    rules: [
                      {
                        required: true,
                        message: '请输入你的密码!',
                      },
                    ],
                  },
                ]"
                type="password"
                placeholder="密码"
              >
                <a-icon
                  slot="prefix"
                  type="lock"
                  style="color: rgba(0, 0, 0, 0.25)"
                />
              </a-input-password>
            </a-form-item>
            <a-form-item>
              <a-input
                style="width: 50%"
                v-decorator="[
                  'code',
                  {
                    rules: [
                      {
                        required: true,
                        message: '请输入正确的验证码!',
                      },
                    ],
                  },
                ]"
                placeholder="验证码"
              >
                <a-icon
                  slot="prefix"
                  type="safety-certificate"
                  style="color: rgba(0, 0, 0, 0.25)"
                />
              </a-input>
              <span class="code" v-show="Codeshow">{{ Code }}</span>
              <button
                type="button"
                class="btnCode"
                @click="btnCode"
                ref="btnCode"
              >
                获取验证码
              </button>
            </a-form-item>
            <a-form-item>
              <a-checkbox
                v-decorator="[
                  'remember',
                  {
                    valuePropName: 'checked',
                    initialValue: true,
                  },
                ]"
              >
                记住我
              </a-checkbox>
              <a class="login-form-forgot" href=""> 忘记密码 </a>
              <br />
              <a-button
                type="primary"
                html-type="submit"
                class="login-form-button"
              >
                登录
              </a-button>
              <br />
              或者
              <a @click="showlore"> 快速注册! </a>
            </a-form-item>
          </a-form>
        </div>
        <div v-else class="showre">
          <a @click="showlore">《《返回登录</a>
          <form action="#">
            <a-input placeholder="注册账号" v-model="name" />
            <br />
            <br />
            <a-input placeholder="电子邮箱" v-model="email" />
            <br />
            <br />
            <a-input-password placeholder="密码" v-model="pwd" />
            <br />
            <br />
            <a-input-password placeholder="确认密码" v-model="ypwd" />
            <br />
            <br />
            <a-button
              type="primary"
              style="margin: auto; display: block; width: 300px"
              @click="regbtn"
            >
              注册
            </a-button>
          </form>
        </div>
      </div>
    </div>
    <div class="footer">
      <p>
        Copyright © 2018-2024 EMI. ALL rights reserved
        南京易米云通网络科技有限公司 版权所有 苏ICP备14035390号-4
      </p>
    </div>
  </div>
</template>
<script>
import { getCode, getuser, Register } from "../api/https";
export default {
  data() {
    return {
      Code: "",
      Codeshow: false,
      s: 60,
      showlogin: true,
      name: "",
      pwd: "",
      ypwd: "",
      email: "",
      timer: "",
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: "normal_login" });
  },
  created() {},
  destroyed() {
    clearInterval(this.timer);
  },
  methods: {
    handleSubmit(e) {
      this.login();
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("获取的数据为: ", values);
        }
      });
    },
    //获取登录验证
    login() {
      getuser({
        name: this.form.getFieldValue("userName"),
        pwd: this.form.getFieldValue("password"),
        vercode: this.form.getFieldValue("code"),
      }).then((res) => {
        console.log(res);
        if (res.code == 0) {
          this.$router.push('/home');
        }
      });
    },
    regbtn() {
      this.register();
    },
    register() {
      if (this.pwd == this.ypwd) {
        var password = this.pwd;
      } else {
        alert("两次密码不一致");
        return;
      }
      Register({
        name: this.name,
        pwd: password,
        emails: this.email,
      }).then((res) => {
        console.log(res);
      });
    },
    //获取验证码
    GetCode() {
      getCode().then((res) => {
        console.log(res);
        this.Code = res.msg;
      });
    },
    showlore() {
      if (this.showlogin == true) {
        this.showlogin = false;
      } else if (this.showlogin == false) {
        this.showlogin = true;
      }
    },
    btnCode() {
      this.Codeshow = true;
      this.GetCode();
      //60秒后可重新获取
      this.$refs.btnCode.disabled = true;
      //按钮禁用后颜色
      this.$refs.btnCode.style.background = "#ccc";
      //按钮禁用后内容变化
      this.$refs.btnCode.innerHTML = "重新获取(" + this.s + "s)";
      this.timer = setInterval(() => {
        this.s--;
        this.$refs.btnCode.innerHTML = "重新获取(" + this.s + "s)";
        if (this.s == 0) {
          this.$refs.btnCode.disabled = false;
          this.$refs.btnCode.style.background = "#1890ff";
          this.$refs.btnCode.innerHTML = "获取验证码";
          this.s = 60;
          //清除定时器
          clearInterval(this.timer);
        }
      }, 1000);
    },
  },
};
</script>
<style lang="less" scoped>
.footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;  
  font-size: 18px;
  line-height: 50px;
  text-align: center;
}
.showre {
  form {
    padding: 0 20px;
    padding-top: 10px;
  }
}
.btnCode {
  margin-left: 20px;
  background: #1890ff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0 10px;
  height: 40px;
  cursor: pointer;
}
.btnCode:hover {
  background: #69b7ff;
}

.code {
  display: inline-block;
  margin-left: 30px;
  font-weight: bold;
  font-size: 18px;
}
.main {
  width: 100%;
  height: 100vh;
  background: url("../assets/loginbg.png") no-repeat center center;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.down {
  width: 160px;
  height: 160px;
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 10px;
  img {
    width: 160px;
    height: 160px;
    border-radius: 10px;
  }
}
.content {
  width: 1060px;
  height: 560px;
  background: white;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  position: relative;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  > div {
    width: 50%;
    height: 100%;
  }
  .pic {
    width: 449px;
    height: 366px;
    background: url("../assets/pic.png") no-repeat center center;
    background-size: cover;
    margin: 0 auto;
    margin-top: 100px;
  }
  > img {
    position: absolute;
    top: -74px;
    left: 43%;
  }
  .login {
    padding: 0 50px;
    > img {
      display: block;
      margin: 85px 0 50px 45px;
    }
    > div {
      .login-form-button {
        display: block;
        width: 300px;
        margin: auto;
      }
    }
  }
}
</style>
